.headerOption {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 20px;
    color: gray;
    cursor: pointer;
}
  
.headerOption__icon {
    object-fit: contain;
    height: 25px !important;
    width: 25px !important;
}
  
.headerOption__title {
    margin: 0;
    font-size: 12px;
    font-weight: 400;
}
  
.headerOption:hover {
    color: #000;
}